<template>
  <li @click="$router.push('/detail?id=' + book._id)">
    <div class="left">
      <van-image width="65" height="80" :src="book.cover | ImgUrl" />
    </div>
    <div class="right">
      <div class="bxr">
        <span class="title">{{ book.title }}</span>
        <span class="icon over" v-if="book.allowMonthly">完结</span>
        <span class="icon conti" v-else>连载</span>
        <p class="shortIntro">{{ book.shortIntro }}</p>
        <span class="card">{{ book.majorCate }}</span>
      </div>
    </div>
  </li>
</template>
<script>
export default {
  props: ["book"],
  filters: {
    ImgUrl: function(n) {
      return decodeURIComponent(n.replace(/\/agent\//gi, ""));
    },
  },
};
</script>
<style lang="less" scoped>
li {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;
  .left {
    width: 65px;
    height: 80px;
    img {
      width: 100%;
      vertical-align: bottom;
    }
  }
  .right {
    padding-left: 10px;
    width: 80%;
    .bxr {
      height: 80px;
      border-bottom: 1px solid lightgrey;
      position: relative;
      .title {
        font-size: 16px;
      }
      .icon {
        margin-left: 7px;
        display: inline-block;
        width: 24px;
        height: 13px;
        font-size: 12px;
        padding: 1px 2px;
        color: white;
        border-radius: 5px;
        line-height: 15px;
        -webkit-transform: scale(0.8);

        &.over {
          background-color: green;
        }
        &.conti {
          background-color: rgb(184, 51, 32);
        }
      }
      p {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        width: 95%;
        height: 30px;
        margin: 0;
        font-size: 12px;
        color: rgb(153, 153, 153);
      }
      .card {
        position: absolute;
        right: 1px;
        border: 1px solid #ff8400;
        color: #ff8400;
        font-size: 12px;
        border-radius: 2px;
        bottom: 2px;
        -webkit-transform: scale(0.8);
      }
    }
  }
}
</style>
