<template>
  <div>
    <div class="box" v-if="content" @click="toggleOption" ref="box">
      <h1 ref="t">{{ content.title }}</h1>
      <p v-for="(p, index) in cpContent" :key="index" ref="p">{{ p | delP }}</p>
    </div>

    <div class="option" v-if="isoption" @click="toggleOption">
      <header>
        <van-nav-bar right-text="按钮" class="nav">
          <template #left>
            <van-icon
              size="16"
              name="data:image/png;base64,"
              @click="
                $router.push(`/catalogPage?bookId=${test}&sourceId=${source}`)
              "
            />
            <!-- '/catalogPage?bookId=5667b5c224aafd4f3abe7c3f&sourceId=5667b5c224aafd4f3abe7c40' -->

            <span
              style="color:white;"
              @click="
                $router.push(`/catalogPage?bookId=${test}&sourceId=${source}`)
              "
              >返回</span
            >
          </template>

          <template #title>
            <div class="center">
              <h4>{{ content.title }}</h4>
            </div>
          </template>
          <template #right>
            <van-icon name="ellipsis" size="22" color="white" />
          </template>
        </van-nav-bar>
      </header>
      <div class="bottom">
        <div class="model">
          <div class="bxm">
            <span
              class="btn"
              v-for="(item, index) in models"
              :key="index"
              :class="act == index ? 'active' : ''"
              @click.stop="toggleAct(index)"
              >{{ item }}</span
            >
          </div>
        </div>
        <div class="to">
          <div class="bxto">
            <span class="pre" @click.stop="prePage" ref="pre">上一章</span>
            <span class="mulu">目录</span>
            <span class="next" @click.stop="nextPage" ref="next">下一章</span>
          </div>
        </div>
      </div>
    </div>
    <van-button
      class="load"
      loading
      type="primary"
      loading-type="spinner"
      v-if="cpContent.length == 0"
    />
  </div>
</template>
<script>
import { getRead } from "@/apis/index.js";

export default {
  data() {
    return {
      source: this.$route.query.source,
      bookid: this.$route.params.bookid,
      test: this.$route.query.test,
      order: parseInt(this.$route.params.order),
      content: null,
      cpContent: [],
      isoption: false,
      models: ["默认", "夜间", "护眼"],
      act: 0,
      all: parseInt(this.$route.query.all),
    };
  },
  methods: {
    toggleAct(c) {
      this.act = c;
      //   console.log(c);
      if (c == 1) {
        this.$refs.box.style.backgroundColor = "black";
        this.$refs.t.style.color = "rgb(136,136,136)";
        this.isoption = false;
      }
      if (c == 2) {
        this.$refs.box.style.backgroundColor = "rgb(184,205,141)";
        this.$refs.t.style.color = "black";
        this.isoption = false;
      }
      if (c == 0) {
        this.$refs.box.style.backgroundColor = "rgb(238, 230, 221)";
        this.$refs.t.style.color = "black";
        this.isoption = false;
      }
    },
    scroll() {
      console.log(111);
    },
    toggleOption() {
      this.isoption = !this.isoption;
    },

    nextPage() {
      if (this.order != this.all) {
        getRead(this.bookid, (this.order += 1)).then((res) => {
          this.content = res.data;
          this.cpContent = res.data.cpContent.match(/<p>.*?<\/p>/gim);
          this.$router.push({
            path: `/chapters2/${this.bookid}/${this.order}`,
          });
        });
      } else {
        this.$refs.next.style.color = "rgb(168, 168, 168)";
      }
    },

    prePage() {
      if (this.order > 1) {
        getRead(this.bookid, (this.order -= 1)).then((res) => {
          this.content = res.data;
          this.cpContent = res.data.cpContent.match(/<p>.*?<\/p>/gim);
          this.$router.push({
            path: `/chapters2/${this.bookid}/${this.order}`,
          });
        });
      } else {
        // console.log(111);
        this.$refs.pre.style.color = "rgb(168, 168, 168)";
      }
    },
  },
  created() {
    getRead(this.bookid, this.order).then((res) => {
      this.content = res.data;
      this.cpContent = res.data.cpContent.match(/<p>.*?<\/p>/gim);
    });
  },
  watch: {
    order(n) {
      if (n == 1) {
        this.$refs.pre.style.color = "rgb(168, 168, 168)";
      } else {
        this.$refs.pre.style.color = "white";
      }
      if (n == this.all) {
        this.$refs.next.style.color = "rgb(168, 168, 168)";
      } else {
        this.$refs.next.style.color = "white";
      }
    },
  },
  mounted() {},
  updated() {
    if (this.isoption) {
      if (this.order == 1) {
        this.$refs.pre.style.color = "rgb(168,168,168)";
      }
      if (this.order == this.all) {
        this.$refs.next.style.color = "rgb(168, 168, 168)";
      }
    }
  },
  filters: {
    delP(n) {
      return n.replace("<p>", "").replace("</p>", "");
    },
  },
};
</script>
<style lang="less" scoped>
.box {
  margin: 0;
  height: 100%;
  padding-top: 25px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: rgb(238, 230, 221);
  h1 {
    margin: 0;
    font-weight: 400;
    padding-bottom: 15px;
    font-size: 30px;
  }
  p {
    text-indent: 44px;
    margin: 0;
    padding-top: 15px;
    font-size: 22px;
    color: rgb(111, 112, 105);
  }
}
.option {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  header {
    height: 42px;
    .van-nav-bar {
      height: 42px;
      line-height: 42px !important;
      background-color: rgba(32, 31, 31, 0.8);
      .van-nav-bar__text {
        color: white;
      }
      h4 {
        color: white;
        margin: 0;
        font-size: 14px;
        text-align: center;
      }
    }
  }
  .bottom {
    width: 100%;
    height: 120px;
    background-color: rgba(32, 31, 31, 0.8);
    position: fixed;
    bottom: 0;
    left: 0;
    .model {
      width: 100%;
      .bxm {
        margin: 0 auto;
        width: 90%;
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
        .btn {
          text-align: center;
          line-height: 30px;
          width: 100px;
          height: 30px;
          border: 1px solid rgb(83, 83, 83);
          border-radius: 6px;
          color: white;
          &.active {
            color: rgb(185, 50, 33);
            border: 1px solid rgb(185, 50, 33);
          }
        }
      }
    }
  }
  .to {
    .bxto {
      margin: 0px auto;
      margin-top: 20px;
      width: 88%;
      display: flex;
      justify-content: space-between;
      span {
        color: white;
        &.mulu {
          color: rgb(168, 168, 168);
        }
        &.pre {
          color: white;
        }
      }
    }
  }
}
.load {
  position: absolute;
  top: 46%;
  left: 43%;
  z-index: 20;
  background-color: #999;
  border: none;
}
</style>
