<template>
  <div class="box">
    <header>
      <van-nav-bar right-text="按钮" class="nav">
        <template #left>
          <van-icon
            size="16"
            name="data:image/png;base64,"
            @click="$router.push('/detail?id=' + $route.query.bookId)"
          />
          <span
            style="color:white"
            @click="$router.push('/detail?id=' + $route.query.bookId)"
            >返回</span
          >
        </template>

        <template #title>
          <div class="center">
            <h4>优质书源</h4>
          </div>
        </template>
        <template #right>
          <van-icon
            @click="$router.push('/')"
            size="24"
            style="marginLeft:10px"
            name="data:image/png;base64,"
          />
        </template>
      </van-nav-bar>
    </header>

    <div class="top">
      <span class="mulu">目录</span>
      <span class="zj">共{{ chapters.length }}章</span>
      <span class="xu">
        <span class="shunxu" @click="reverse" v-if="r">正序</span>
        <span class="shunxu" @click="reverse" v-else>倒序</span>
      </span>
    </div>
    <div class="content">
      <ul>
        <li
          v-for="(item, index) in chapters"
          :key="index"
          @click="
            toReadPage(
              '5667b5c224aafd4f3abe7c3f',
              item.order,
              chapters.length,
              $route.query.sourceId,
              $route.query.bookId
            )
          "
        >
          <span class="num">{{ item.order }}</span
          >{{ item.title }}
        </li>
      </ul>
    </div>
    <van-button
      class="load"
      loading
      type="primary"
      loading-type="spinner"
      v-if="chapters.length == 0"
    />
  </div>
</template>
<script>
import { getCatalog } from "@/apis/index.js";

export default {
  data() {
    return {
      chapters: [],
      r: false,
    };
  },
  methods: {
    reverse() {
      this.chapters.reverse();
      this.r = !this.r;
    },
    toReadPage(bookid, order, all, source, test) {
      this.$router.push({
        path: `/chapters2/${bookid}/${order}`,
        query: {
          all,
          source,
          test,
        },
      });
    },
  },
  created() {
    getCatalog(this.$route.query.sourceId).then((res) => {
      this.chapters = res.data.chapters;
    });
  },
};
</script>
<style lang="less" scoped>
.box {
  header {
    .van-nav-bar {
      background-color: rgb(184, 51, 32);
      .van-nav-bar__text {
        color: white;
      }
      h4 {
        color: white;
        margin: 0;
        font-size: 14px;
        text-align: center;
      }
    }
  }
  .top {
    padding: 15px 20px;
    border-bottom: 1px solid rgb(247, 243, 243);

    .mulu {
      font-size: 16px;
      font-weight: bold;
    }
    .zj {
      margin-left: 5px;
      font-size: 14px;
      color: #999;
    }
    .xu {
      float: right;
      font-size: 13px;
    }
  }
  .content {
    padding: 0 20px;
    ul {
      margin: 0;
      padding: 0;
      li {
        padding: 5px 0;
        height: 30px;
        line-height: 33px;
        border-bottom: 1px solid rgb(247, 243, 243);
        font-size: 13px;
        color: #999;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        .num {
          margin-right: 15px;
        }
      }
    }
  }
  .load {
    position: absolute;
    top: 46%;
    left: 43%;
    z-index: 20;
    background-color: #999;
    border: none;
  }
}
</style>
