<template>
  <div class="detail">
    <header>
      <van-nav-bar right-text="按钮" class="nav">
        <template #left>
          <van-icon
            size="16"
            name="data:image/png;base64,"
            @click="$router.push('/')"
          />
          <span style="color:white" @click="$router.push('/')">返回</span>
        </template>

        <template #title>
          <div class="center">
            <h4>书籍详情</h4>
          </div>
        </template>
        <template #right>
          <van-icon
            size="24"
            name="data:image/png;base64,"
            @click="$router.push('/mybooks')"
          />
          <van-icon
            @click="$router.push('/')"
            size="24"
            style="marginLeft:10px"
            name="data:image/png;base64,"
          />
        </template>
      </van-nav-bar>
    </header>
    <main v-if="detail">
      <div class="top">
        <div class="left">
          <van-image width="65" height="88" :src="detail.cover | ImgUrl" />
        </div>
        <div class="right">
          <h5>{{ detail.title }}</h5>
          <p>
            <span class="art">{{ detail.author }}</span>
            &nbsp;&nbsp;|&nbsp;&nbsp;
            <span class="cat">{{ detail.cat }}</span>
            &nbsp;&nbsp;|&nbsp;&nbsp;
            <span class="cat">{{ detail.wordCount | num }}</span>
          </p>
        </div>
      </div>
      <div class="btnbox">
        <van-button
          plain
          type="primary"
          class="btn b1"
          @click="addMybook(detail)"
          v-if="!isExit"
          >加入书架</van-button
        >
        <van-button
          plain
          type="primary"
          class="btn b3"
          v-else
          @click="removeMybook(detail._id)"
          >移出书架</van-button
        >

        <van-button
          plain
          type="primary"
          class="btn b2"
          @click="
            toReadPage(
              '5667b5c224aafd4f3abe7c3f',
              1,
              booksource[0]._id,
              detail._id
            )
          "
          >开始阅读</van-button
        >
      </div>
      <div class="intrbox" ref="intrbox">
        <van-icon
          name="arrow-up"
          class="icon"
          v-if="icon"
          @click="icon = !icon"
        />
        <van-icon
          name="arrow-down"
          class="icon"
          v-if="!icon"
          @click="icon = !icon"
        />
        <p ref="pp" :class="icon ? '' : active">{{ detail.longIntro }}</p>
      </div>

      <div class="catalog" @click="toCatalogPage">
        目录
        <span style="float:right" v-if="booksource"
          >最新：{{ booksource[0].lastChapter }}</span
        >
      </div>

      <div class="comments">
        <h5>热门评论</h5>
        <ul v-if="comments">
          <li v-for="(item, index) in comments.docs" :key="index">
            <div class="left">
              <van-image
                round
                width="2.5rem"
                height="2.5rem"
              />
            </div>
            <div class="right">
              <p class="name">{{ item.author.nickname }}</p>
              <p class="content">{{ item.content }}</p>
            </div>
          </li>
        </ul>
      </div>
    </main>
    <van-button
      class="load"
      loading
      type="primary"
      loading-type="spinner"
      v-if="!detail"
    />
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
import { getDetail } from "@/apis/index.js";
import { getBookSorce } from "@/apis/index.js";
import { getComments } from "@/apis/index.js";

export default {
  data() {
    return {
      detail: null,
      icon: false,
      active: "active",
      booksource: null,
      comments: null,
    };
  },
  created() {
    getDetail(this.$route.query.id).then((res) => (this.detail = res.data));
    getBookSorce(this.$route.query.id).then((res) => {
      this.booksource = res.data;
    });
    getComments(this.$route.query.id).then((res) => {
      this.comments = res.data;
    });
  },
  computed: {
    ...mapState(["mybooks"]),
    isExit() {
      return this.mybooks.some((item) => item._id === this.detail._id);
    },
  },
  methods: {
    ...mapMutations([
      "addMybook",
      "removeMybook",
      "getBookSorce",
      "getComments",
    ]),

    toCatalogPage() {
      this.$router.push({
        path: "/catalogPage",
        query: {
          bookId: this.detail._id,
          sourceId: this.booksource[0]._id,
        },
      });
    },
    toReadPage(bookid, order, source, test) {
      this.$router.push({
        path: `/chapters2/${bookid}/${order}`,
        query: {
          source,
          test,
        },
      });
    },
  },
  watch: {},
  filters: {
    num: function(n) {
      if (n.toString().length >= 7) {
        const num = n / 10000;
        const newnum = parseFloat(num).toFixed(0);
        return newnum + "万字";
      }
    },
    ImgUrl: function(n) {
      return decodeURIComponent(n.replace(/\/agent\//gi, ""));
    },
  },
};
</script>
<style lang="less" scoped>
.detail {
  margin: 0;
  padding: 0;
  header {
    .van-nav-bar {
      background-color: rgb(184, 51, 32);
      .van-nav-bar__text {
        color: white;
      }
      h4 {
        color: white;
        margin: 0;
        font-size: 14px;
        text-align: center;
      }
    }
  }
  main {
    padding: 20px;
    .top {
      display: flex;
      justify-content: space-between;
      .left {
        .van-image {
          vertical-align: bottom;
        }
      }
      .right {
        width: 80%;
        padding-left: 10px;
        h5 {
          margin: 0;
          font-weight: normal;
          font-size: 16px;
        }
        p {
          margin: 0;
          margin-top: 10px;
          font-size: 13px;
          color: rgb(153, 153, 153);
          .art {
            color: rgb(184, 51, 32);
          }
        }
      }
    }
    .btnbox {
      display: flex;
      justify-content: space-between;
      padding-top: 20px;
      padding-bottom: 20px;

      .btn {
        width: 150px;
        font-size: 16px;
        &.b1 {
          border: 1px solid rgb(184, 51, 32);
          color: rgb(184, 51, 32);
        }
        &.b2 {
          background-color: rgb(184, 51, 32);
          border: 1px solid rgb(184, 51, 32);
          color: white;
        }
        &.b3 {
          background-color: rgb(153, 153, 153);
          border: 1px solid rgb(153, 153, 153);
          color: white;
        }
      }
    }
    .intrbox {
      padding: 20px 0;
      position: relative;
      width: 100%;
      height: 100%;
      //   background-color: lightgrey;
      border-top: 1px solid rgb(235, 235, 235);
      border-bottom: 1px solid rgb(235, 235, 235);
      .icon {
        position: absolute;
        right: 1px;
        bottom: 6px;
        color: rgb(138, 138, 138);
      }
      p {
        margin: 0;
        font-size: 14px;

        &.active {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 4;
          overflow: hidden;
        }
      }
    }
    .catalog {
      font-size: 13px;
      padding-top: 20px;
      padding-bottom: 20px;
      border-bottom: 1px solid rgb(235, 235, 235);
      span {
        width: 90%;
        color: #999;
      }
    }
    .comments {
      padding-top: 20px;
      padding-bottom: 20px;
      h5 {
        margin: 0;
        font-weight: normal;
        font-size: 13px;
        padding-bottom: 15px;
      }
      ul {
        li {
          padding-top: 10px;
          padding-bottom: 10px;

          display: flex;
          justify-content: space-between;
          border-bottom: 1px solid rgb(235, 235, 235);
          .right {
            width: 85%;
            .name {
              margin: 0;
              color: #a58d5e;
              font-size: 14px;
            }
            .content {
              margin: 0;
              color: #999;
              font-size: 12px;
              padding-top: 5px;
            }
          }
        }
      }
    }
  }
}
.load {
  position: absolute;
  top: 46%;
  left: 43%;
  z-index: 20;
  background-color: #999;
  border: none;
}
</style>
