// 书本主页
const getDetail = (id) => {
  return window.axios.get("http://novel.kele8.cn/book-info/" + id);
};

// 书籍源
const getBookSorce = (id) => {
  return window.axios.get(
    "http://novel.kele8.cn/book-sources?view=summary&book=" + id
  );
};

// 评论
